import QR from "../../static/js/wxqrcode.js"
export default {
	data() {
		return {
			qrcode: '二维码', // 要生成的二维码值
			menberShip: '1111111111111111111111',
			img: null
		}
	},
	methods: {

	},
	onLoad: function() {
		var img = QR.createQrCodeImg(this.menberShip, {
			size: parseInt(300)
		})
		this.img = img
	}
}
